<template name="index">
	<view class="things_index">
		<scroll-view scroll-y class="page things_padding_100">
			<image class="things_width_full topImage" src="./../../static/images/index/index_top_bg.png"
			 mode="widthFix"></image>
			<view class="flex justify-start align-center bg-white things_width_full fun_list">
				<!-- 一排四个 -->
				<view class="flex justify-between align-center things_width_full functional_area">
					<view class="flex functional_item" data-id="1" @tap="linkToGrabOrderHall">
						<image class="item_image" src="./../../static/images/index/huoche.png" mode="heightFix" />
						<view class="text-center things_font_size_28 things_font_base_color functional_text">找车</view>
					</view>
					<view class="flex functional_item" data-id="2" @tap="linkToGrabOrderHall">
						<image class="item_image" src="./../../static/images/index/huowu.png" mode="heightFix" />
						<view class="text-center things_font_size_28 things_font_base_color functional_text">找货</view>
					</view>
					<view class="flex functional_item" @tap="linkToWallet">
						<image class="item_image" src="./../../static/images/index/wallet.png" mode="heightFix" />
						<view class="text-center things_font_size_28 things_font_base_color functional_text">钱包</view>
					</view>
					<view class="flex functional_item" data-type="1" @click="linkToInterests">
						<image class="item_image" src="./../../static/images/index/interests.png" mode="heightFix" />
						<view class="text-center things_font_size_28 things_font_base_color functional_text">权益</view>
					</view>
				</view>
				<view class="user_fun">
					<!-- 背景 -->
					<image class="things_width_full user_fun_bg_1" src="./../../static/images/index/index_fun_bg1.png" mode="widthFix"></image>
					<!-- 我的钱包、我的订单、发布需求 -->
					<view class="flex justify-center align-center things_width_full user_fun_top">
						<view class="flex justify-center align-center things_font_size_32 text-white user_wallet" @tap="linkToWallet">我的钱包</view>
						<view class="flex justify-center align-center things_font_size_32 text-white user_order" @tap="linkToOrder">我的订单</view>
						<view class="flex justify-center align-center things_font_size_32 text-white user_publish" @tap="linkToDemandClassify">发布的需求</view>
					</view>
					<view class="things_width_full user_fun_bottom">
						<!-- 背景 -->
						<image class="things_width_full user_fun_bg_2" src="./../../static/images/index/index_fun_bg2.png" mode="widthFix"></image>
						<!-- 收藏车辆/收藏货源/充值提现/建设分/馈赠分/分红金/客服热线/推广权益 -->
						<view class="flex justify-center align-center things_width_full user_fun_bottom_box">
							<view class="flex justify-center align-center things_width_full user_fun_bottom_top">
								<view class="flex justify-center align-center things_font_size_32 text-white user_car" @tap="linkToCollectCar">收藏车辆</view>
								<view class="flex justify-center align-center things_font_size_32 text-white user_supply" @tap="linkToCollectFreight">收藏货源</view>
								<view class="flex justify-center align-center things_font_size_32 text-white user_deposit_withdrawal" @tap="linkToRecharge">充值</view>
							</view>
							<view class="flex justify-center align-center things_width_full user_fun_bottom_center">
								<view class="flex justify-center align-center things_font_size_32 text-white user_construction_points" @tap="linkToConstructionPoints">建设分</view>
								<view class="flex justify-center align-center things_font_size_32 text-white user_gift_points" @tap="linkToGiftPoints">馈赠分</view>
								<view class="flex justify-center align-center things_font_size_32 text-white user_deposit_dividend" @tap="linkToDividend">分红金</view>
							</view>
							<view class="flex justify-center align-center things_font_size_32 text-white things_width_full user_fun_bottom_customer_hotline" @tap="makeCall">客服热线020-0121520</view>
							<view class="flex justify-center align-center things_font_size_32 text-white things_width_full user_fun_bottom_promotion_rights" data-type="2" @tap="linkToInterests">推广</view>
						</view>
					</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		name: "index",
		data() {
			return {
				
			};
		},
		onShow() {
			console.log("success")
		},
		/**
		 * 自定义方法
		 */
		methods: {
			/**
			* @description: 跳转我的权益
			* @param {*}
			* @return {*}
			* @author: gitxinghui
			*/
			linkToInterests: function(e) {
				uni.navigateTo({
					url: `/pages/things/user/interests/index?type=${e.currentTarget.dataset.type}`,
				});
			},
			/**
			* @description: 跳转到找货、找车
			* @param {*} type: 类型
			* @return {*}
			* @author: gitxinghui
			*/
			linkToGrabOrderHall: function(e) {
				uni.navigateTo({
					url: `/pages/things/grabOrderHall/hall/index?type=${e.currentTarget.dataset.id}`,
				});
			},
			/**
			* @description: 跳转到我的钱包
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToWallet(e) {
				uni.navigateTo({
					url: `/pages/things/user/wallet/index`,
				});
			},
			/** 
			* @description: 跳转到我的订单
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToOrder(e) {
				uni.navigateTo({
					url: `/pages/things/user/order/classify/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到我发布的需求分类
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToDemandClassify(e) {
				uni.navigateTo({
					url: `/pages/things/user/demand/list/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 收藏的车辆
			* @param {*} type:收藏列表导航下表
			* @return {*}
			* @author: gitxinghui
			*/
			linkToCollectCar(e) {
				uni.navigateTo({
					url: `/pages/things/user/collect/list/index?type=2`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 收藏的货源
			* @param {*} type:收藏列表导航下表
			* @return {*}
			* @author: gitxinghui
			*/
			linkToCollectFreight(e) {
				uni.navigateTo({
					url: `/pages/things/user/collect/list/index?type=1`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到充值页
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToRecharge(e) {
				uni.navigateTo({
					url: `/pages/things/amount/recharge/balance/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到建设分
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToConstructionPoints(e) {
				uni.navigateTo({
					url: `/pages/things/integral/constructionPoints/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到馈赠分
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToGiftPoints(e) {
				uni.navigateTo({
					url: `/pages/things/integral/giftPoints/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到分红金
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToDividend(e) {
				uni.navigateTo({
					url: `/pages/things/integral/dividend/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 拨打电话
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			makeCall(e) {
				uni.makePhoneCall({
					// 手机号
					phoneNumber: '15626236839', 
					// 成功回调
					success: (res) => {
						console.log('调用成功!')	
					},
					// 失败回调
					fail: (res) => {
						console.log('调用失败!')
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "./index.scss";
</style>
